// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import VueLazyload from 'vue-lazyload'
import App from './App'
import router from './router'
import './assets/font/iconfont.css'
import './assets/css/base.css'
import store from '../store/index2'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

Vue.config.productionTip = false
axios.defaults.baseURL = 'http://localhost:3333/'
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'
Vue.prototype.$http = axios
Vue.use(VueLazyload, {
  preLoad: 1.3, // lazyload
  error: require('./assets/images/err.png'), // .
  loading: require('./assets/images/loading.gif'), // loading
  attempt: 1, // .3.
  listenEvents: ['scroll'] // ['scroll', 'wheel', 'mousewheel', 'resize', 'animationend', 'transitionend', 'touchmove'].
  // ['touchmove'].scroll ,.
})
Vue.use(VueAwesomeSwiper)

/* eslint-disable no-new */
new Vue({
  el: '#app', router, store, render: h => h(App)
})
